.btn-reset {
}
.burger {
  width: 37px;
  height: 29px;
  background-color: $bg-color;
  display: flex;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-end;
  align-content: space-between;
}
.burger__line {
  background-color: rgb(0, 0, 0);
  border-radius: 15px;
}
.burger__line:nth-child(1),
.burger__line:nth-child(3) {
  width: 24px;
  height: 3px;
}
.burger__line:nth-child(2),
.burger__line:nth-child(4) {
  width: 37px;
  height: 3px;
}
