html {
  box-sizing: border-box;
}

*,
::after,
::before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
  border: 0;
}

ol[class],
ul[class] {
  list-style: none;
}

a {
  text-decoration: none;
}

button,
a {
  cursor: pointer;
}

img {
  max-width: 100%;
  display: block;
}

button,
input,
select,
textarea {
  color: inherit;
  border: 0;
}

input,
textarea {
  border: none;
  border-radius: 0;
  outline: none;
}

html,
body {
  height: 100%;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.main {
  flex: 1 0 auto;
}

.footer {
  flex: 0 0 auto;
}

.container {
  max-width: 1133px;
  width: 100%;
  margin: 0 auto;
  padding: 0 2px;
}

body {
  background-color: #fff;
  font-family: "OpenSans", "Helvetica", "Arial", sans-serif;
  font-size: calc(14px + 2 * (100vw - 340px) / 793);
  font-family: "OpenSans", sans-serif;
  font-weight: 400;
  line-height: 1.6875;
  letter-spacing: normal;
  color: #5a5a5a;
}

.logo {
  width: 97px;
  height: 33px;
}

.link {
  font: inherit;
  color: rgba(54, 56, 56, 0.9019607843);
  font-family: "Montserrat";
  font-weight: 700;
  line-height: 1.375;
  font-size: 16;
}

.btn {
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
  font-size: 16px;
  color: #fffff0;
  align-items: center;
  background-color: #363838;
  border-radius: 21px;
  border: 1px #363838 solid;
  transition: background-color, color, 0.5s ease-out;
  user-select: none;
}
.btn:hover {
  background-color: #fffff0;
  color: #363838;
  color: #363838;
}
.btn:active {
  transform: scale(0.96);
}

.btn-tag {
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
  font-size: 14px;
  color: #8f8d8d;
  align-items: center;
  background-color: transparent;
  border-radius: 21px;
  border: 1px solid #8f8d8d;
  transition: background-color, color, 0.5s ease-out;
  user-select: none;
}
.btn-tag:hover {
  background-color: #ffef35;
  color: #363838;
  border-color: #ffef35;
}
.btn-tag:active {
  transform: scale(0.96);
}

.play {
  width: 80px;
  height: 80px;
  background-color: #979797;
  border-radius: 50px;
  transition: all 0.3s;
}
.play__go {
  width: 0;
  height: 0;
  border-top: 18px solid transparent;
  border-bottom: 18px solid transparent;
  border-left: 28px solid rgba(255, 255, 255, 0.9294117647);
  margin: 22px auto;
  display: block;
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.8509803922);
}
.play:active {
  transform: scale(0.96);
}

.input {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #a1a1a1;
  align-items: left;
  border: 1px solid #dfdfdf;
  border-radius: 20px;
  background-color: transparent;
  transition: background-color, color, 0.5s ease-out;
}
.input:hover {
  color: #363838;
}
.input:focus {
  border-color: #363838;
  color: #363838;
}

.title {
  color: #363838;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 110px;
  line-height: 134px;
  letter-spacing: 0px;
}

.sub-title {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 60px;
  line-height: 80px;
  letter-spacing: 0px;
  color: #363838;
}

.title-box {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 36px;
  line-height: 51px;
  letter-spacing: 0px;
  color: #363838;
}

.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 56px;
  padding-top: 55px;
  padding-bottom: 55px;
}

.burger {
  width: 37px;
  height: 29px;
  background-color: #fff;
  display: flex;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-end;
  align-content: space-between;
}

.burger__line {
  background-color: rgb(0, 0, 0);
  border-radius: 15px;
}

.burger__line:nth-child(1),
.burger__line:nth-child(3) {
  width: 24px;
  height: 3px;
}

.burger__line:nth-child(2),
.burger__line:nth-child(4) {
  width: 37px;
  height: 3px;
}

.footer {
  background-color: #dfdfdf;
}

.footer__container {
  display: flex;
  justify-content: space-between;
  padding-top: 60px;
  padding-bottom: 60px;
}

.footer__form {
  display: flex;
  flex-direction: column;
}

.footer__form-send {
  display: flex;
}

.footer__form-title {
  font-size: 16px;
  line-height: 0;
  padding-bottom: 14px;
}

.footer__form-text {
  max-width: 474px;
  width: 100%;
  padding-bottom: 16px;
}

.footer__form-link {
  padding-bottom: 22px;
}

.form__input {
  width: 100%;
  padding: 6px 12px;
  border: 1px solid #383636;
}

.form__btn {
  max-width: 160px;
  width: 100%;
  padding: 6px 12px;
}

.footer__copy {
  background-color: #363838;
  text-align: center;
  padding-top: 41px;
  padding-bottom: 41px;
}

.footer__copy-link {
  font-family: "OpenSans", sans-serif;
  font-weight: 400;
  line-height: 1.75;
  color: #a1a1a1;
}